.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    height: 60px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    width: 60%;
    border-radius: 5px;
    padding: 0 20px;
}

.LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    /* border-radius: 50px;
    height: 48px;
    width: 48px; */
}

.LoadingTodo-completeIcon {
}

.LoadingTodo-deleteIcon {
    /* position: absolute;
    top: -24px;
    right: 0; */
}

.LoadingTodo-container {
    background: linear-gradient(90deg, #787b80, #c3c5c9);
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
